html, body {
  background-color: #21232A;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  padding: 20px;
}

button {
  font-size: 1.3rem;
  margin-right: 10px;
  padding: 10px;
}

input {
  font-size: 1.2rem;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-left: 10px;
  overflow: hidden;
}

.car p {
  padding: 5px;
  text-align: center;
}

.car img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}